const SEND_MSG = 'dialogs/SEND-MSG';

let initialState = {
    dialogs: [
        {
            id: 1,
            name: 'Alex',
            avatarImg: "https://shapka-youtube.ru/wp-content/uploads/2021/02/prikolnaya-avatarka-dlya-patsanov.jpg"
        },
        {
            id: 2,
            name: 'Denis',
            avatarImg: "data:image/jpeg;base64,"
        },
        {
            id: 3,
            name: 'Petr',
            avatarImg: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT4VC8WL-ap1mStCeC7nilr3YxMr5CuowNy9w&usqp=CAU"
        },
        {
            id: 4,
            name: 'Vasiliy',
            avatarImg: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRl3FEmPBlpiCLAA382CDq1YNmA6gpICLmcWQ&usqp=CAU"
        },
    ],
    incomeMessages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'How are you'},
        {id: 3, message: 'Yo Yo'},
        {id: 4, message: 'buy'},
    ],
    outcomeMessages: [
        {id: 1, message: 'Hi Hi'},
        {id: 2, message: 'I am fine'},
        {id: 3, message: 'Yoooo'},
        {id: 4, message: 'Byu Byu'},
    ],
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MSG:
            const newMsg = {
                id: 5,
                message: action.message,
            }
            return {
                ...state,
                outcomeMessages: [...state.outcomeMessages, newMsg],
            }
        default:
            return state
    }
}


export const sendMsg = (message) => ({type: SEND_MSG, message});


export default dialogsReducer;